import React, {useState} from 'react';

import styles from "./IndBlogSlider.module.scss"
import BlogSlider from "../../global/BlogSlider/BlogSlider";
function IndBlogSlider({type}) {
    const [isDataEmpty,setIsDataEmpty] = useState(false)
    return (
        <div className={styles['IndBlogSlider']}>
            {isDataEmpty && <h3 className={`${styles["IndBlogSlider__title"]} titleDef container`}>Similar <span
                className='lightBlueText'>blogs</span></h3>}
            <BlogSlider type={type} isFromIndBlog={true} setIsDataEmpty={setIsDataEmpty}/>
        </div>
    );
}

export default IndBlogSlider;